#homeSliderParent {
  height: 80px;
  /*
      WHAT IS NEW IN 3.3: "Added transforms to improve carousel performance in modern browsers."
      Need to override the 3.3 new styles for modern browsers & apply opacity
    */
}
#homeSliderParent .carousel-fade .carousel-inner .item {
  opacity: 0;
  transition-property: opacity;
}
#homeSliderParent .carousel-fade .carousel-inner .active {
  opacity: 1;
}
#homeSliderParent .carousel-fade .carousel-inner .active.left,
#homeSliderParent .carousel-fade .carousel-inner .active.right {
  left: 0;
  opacity: 0;
  z-index: 1;
}
#homeSliderParent .carousel-fade .carousel-inner .next.left,
#homeSliderParent .carousel-fade .carousel-inner .prev.right {
  opacity: 1;
}
#homeSliderParent .carousel-fade .carousel-control {
  z-index: 2;
}
@media all and (transform-3d), (-webkit-transform-3d) {
  #homeSliderParent .carousel-fade .carousel-inner > .item.next,
  #homeSliderParent .carousel-fade .carousel-inner > .item.active.right {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  #homeSliderParent .carousel-fade .carousel-inner > .item.prev,
  #homeSliderParent .carousel-fade .carousel-inner > .item.active.left {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  #homeSliderParent .carousel-fade .carousel-inner > .item.next.left,
  #homeSliderParent .carousel-fade .carousel-inner > .item.prev.right,
  #homeSliderParent .carousel-fade .carousel-inner > .item.active {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
