/* COMMON RADIO AND CHECKBOX STYLES  */
label {
  font-weight: lighter;
}
input[type=radio],
input[type=checkbox] {
  /* hide original inputs */
  visibility: hidden;
  position: absolute;
}
input[type=radio] + label,
input[type=checkbox] + label {
  cursor: pointer;
}
input[type=radio] + label:before,
input[type=checkbox] + label:before {
  height: 16px;
  margin-right: 4px;
  content: " ";
  display: inline-block;
  vertical-align: -3px;
  border: 2px solid #60c06c;
  border-radius: 10px;
  transition: 0.3s;
}
/* DEFAULT */
input[type=radio] + label:before {
  border-radius: 50%;
  width: 16px;
  margin-right: 17px;
}
input[type=checkbox] + label:before {
  border-radius: 2px;
  width: 26px;
}
/* CHECKED */
input[type=radio]:checked + label:before {
  box-shadow: inset 0 -1px 3px #60c06c, inset 0 0 1px #222222, inset 0 0 0 4px #60c06c;
  border: 1px solid #60c06c;
}
input[type=checkbox]:checked + label:before {
  box-shadow: inset 0 -1px 3px #60c06c, inset 0 0 1px #222222, inset 0 0 0 4px #60c06c;
}
.radio-box label {
  font-size: 14px;
  font-family: 'montserratlight';
  color: #3e3e3e;
  padding: 5px 0px;
}
.radio-bg {
  background: #edf8ff;
  padding: 10px 0px;
  margin-bottom: 30px;
}
.radio-box input[type=text] {
  border: none;
  background-color: #EDF8FF;
  border-bottom: 1px solid #bcd8eb;
}
.radio-box label input:focus ~ label,
input:valid ~ label,
textarea:focus ~ label,
textarea:valid ~ label {
  font-size: 14px!important;
}
