/*form*/
input:focus ~ label,
input:valid ~ label,
textarea:focus ~ label,
textarea:valid ~ label {
  color: #8b8b8b;
  top: -10px;
  -webkit-transition: all 125ms ease;
  transition: all 125ms ease;
  font-family: 'montserratlight';
  font-size: 11px;
}
.styled-input {
  position: relative;
}
.styled-input label {
  padding-left: 0;
  color: #767676;
  position: absolute;
  top: -2px;
  left: 0;
  -webkit-transition: all .25s ease;
  transition: all .25s ease;
  /* pointer-events: none;*/
  font-family: 'montserratlight';
  font-size: 11px;
  pointer-events: none;
}
.styled-input input[type=password] {
  /*    margin: 15px 0px;*/
  margin-bottom: 30px;
  width: 100%;
  border-bottom: 1px solid #cadeeb!important;
  border: none;
  box-shadow: none;
  background: #F9F9F9;
  font-size: 16px;
  min-height: 36px;
}
.styled-input input[type=text] {
  /*    margin: 15px 0px;*/
  margin-bottom: 30px;
  width: 100%;
  border-bottom: 1px solid #cadeeb!important;
  border: none;
  box-shadow: none;
  background: #F9F9F9;
  font-size: 16px;
  padding-top: 9px;
  min-height: 36px;
}
.styled-input input[type=password],
input[type=text],
input[type=email],
input[type=number]:focus {
  outline: none;
}
.styled-input input:focus ~ span,
textarea:focus ~ span {
  width: 100%;
  -webkit-transition: all 75ms ease;
  transition: all 75ms ease;
}
.styled-input input ~ span,
textarea ~ span {
  display: block;
  width: 0;
  height: 1px;
  background: #0c67a6;
  position: absolute;
  bottom: 30px!important;
  left: 0;
  -webkit-transition: all 125ms ease;
  transition: all 125ms ease;
}
